/* ======================================
 *
 FOOTER
 *
 @mixin =======================================
 *# 1.0 Site footer
 *# 1.1 Footer column
 *# 2.0 Container copyright
 *# 3.0 Media Queries
 *-------------------------------------------------------------- */

/* -------------------------------------------------------------
 1.0 Site footer
 ------------------------------------------------------------- */
.footer {
	background-color: $bg-footer;
	background-image: url('../images/bg-footer.png');
	background-position: bottom;
	background-size: 60px auto;
	background-repeat: repeat-x;
	padding-bottom: 10px; }

.site-footer {
	display: inline-block;
	position: relative;
	color: $light-text;

	a {
		color: $light-text; }

	.copyright {
		display: inline-block;
		margin: 33px 0 0 50px;
		font-family: $secundary-font;
		font-size: 1.6rem;
		line-height: 25px;

		@media screen and (max-width: 1023px) {
			width: 100%;
			text-align: center;
			margin-left: 0px; } } }


.container-urso {
	.container {
		@media screen and (min-width: 1024px) {
			height: 70px; } }
	p {
		font-size: 14px;
		font-weight: 400;
		color: #8a8e97;
		font-family: $secundary-font;
		text-transform: uppercase;
		margin: 10px 0 25px;
		float: left; }

	img {
		@media screen and (max-width: 1023px) {
			display: block;
			width: 280px;
			margin: 20px auto 0px; }

		@media screen and (min-width: 1024px) {
			width: 330px;
			position: absolute;
			right: 0;
			bottom: 0; } } }
/* -------------------------------------------------------------
 1.1 Footer column
 ------------------------------------------------------------- */
.footer-column {
	padding: 33px 0 20px;
	@media screen and (min-width: 1024px) {
		float: left; }
	@media screen and (max-width: 1023px) {
		&.midias-ft {
			width: 205px;
			margin: auto; } }

	&:first-child {
		padding-left: 0px;
		padding-right: 0px;
		@media screen and (min-width: 1024px) {
			margin-left: 8.33333%; }
		@media screen and (max-width: 1023px) {
			text-align: center; } }


	&.wrap-customer-data {
		@media screen and (min-width: 1024px) {
			width: 54.35%;
			margin-left: 3.521%; }
		@media screen and (min-width: 1100px) {
			margin-left: 7.521%; } }

	h3 {
		font-size: 20px;
		font-family: $main-font;
		font-weight: bold;
		color: #4a96d0;
		line-height: 1;
		text-transform: uppercase;
		//margin: 21px 0 0 0
		margin: 0;
		padding: 0; }

	&:nth-child(2) {
		h3 {
			@media screen and (min-width: 768px) {
				margin-left: 29px; } } }
	&:nth-child(3) {
		h3 {
			@media screen and (min-width: 768px) {
				margin-left: 30px; } }

		ul {
			margin-top: 10px;
			@media screen and (min-width: 768px) {
				margin-left: 30px; }
			li {
				width: 100%;
				float: left;
				&:first-child {
					font-size: 13.56px;
					text-transform: lowercase; }
				.wrap-ico {
					width: 15px;
					float: left;
					margin-right: 5px;
					text-align: left; }
				span {
					float: left;
					line-height: 1.2;
					@media screen and (min-width: 1024px) {
						width: 132px; } }
				&:first-child {
					span {
						line-height: 1.4; } }
				&:nth-child(2), &:nth-child(3) {
					span {
						line-height: 1.1; } } } } }

	&:nth-child(4) {
		h3 {
			margin-left: 5px;
			margin-bottom: 10px; }

		.social-fields {
			height: 38px;
			margin: 0px 13px 0 20px;
			width: 38px; } }

	.box-logo {
		max-width: 255px; }

	.wpcf7 {
		.wpcf7-form {
			br {
				display: none; }
			input, textarea {
				display: block;
				font-family: $main-font;
				font-size: 12px;
				font-weight: normal;
				line-height: 40px;
				color: #737373;
				margin-bottom: 10px;
				border: 2px solid $light-text;
				@include placeholder(#737373);
				&.wpcf7-not-valid {
					margin-bottom: 0px; } }
			textarea {
				height: 103px;
				resize: none;
				border: 2px solid $light-text; }
			.wpcf7-submit {
				border: none;
				background: #a27e3d;
				font-size: 16px;
				font-family: 'ConthraxSb-Regular';
				color: #fff;
				height: 40px; }
			span.wpcf7-not-valid-tip {
				text-transform: uppercase;
				margin-bottom: 10px;
				padding-left: 15px; }
			> p {
				margin-bottom: 0px; } } }

	.wrap-info-footer {
		display: inline-block;
		&:first-child {
			@media screen and (min-width: 600px) {
				//margin-bottom: 83px
				margin-bottom: 90px; } } }

	ul {
		list-style-type: none;
		padding-left: 0px;
		li {
			margin-bottom: 10px;
			font-size: 14px;
			font-weight: 500;
			text-transform: uppercase;
			color: #585858;
			a {
				color: inherit; }
			@media screen and (max-width: 1199px) {
				font-size: 14px; } } } }

.site-footer {
	.footer-left, .footer-right {
		float: left; }
	.footer-left {
		display: inline-block; }
	.footer-right {
		text-transform: uppercase;
		display: inline-block;
		h3 {
			font-weight: normal; } } }

#footer-menu {
	display: none;
	@media screen and (min-width: 1024px) {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		margin-top: 35px;
		padding-left: 0px;
		list-style-type: none;
		font-family: $secundary-font;
		//text-transform: uppercase
		ul {
			margin-top: 20px;
			list-style-type: none;
			margin-left: 0px;
			padding-left: 0px; }

		a {
			color: #8a8e97;
			font-size: 25px; } } }

#sac-wrap {
	@media screen and (min-width: 768px) {
		margin-left: 8.33333%; } }
.attendance-list {
	margin-top: 10px;
	li {
		line-height: 20px;
		&:last-child {
			line-height: normal;
			margin-bottom: 0px; }
		a {
			img {
				float: left;
				margin-right: 6px; } } } }

.logo-ft {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	margin: 0;
	padding: 0;
	text-align: center;

	img {
		max-width: 214px; } }

.text-ft {
	width: 100%;
	margin: 3px 0 25px 0;
	padding: 0;
	font-size: 1.4rem;
	line-height: 17px;
	color: #5a5a5a; }


/*- infos ft -*/
.info-ft {
	width: 100%;
	padding: 0 0 20px 0;

	p, a {
		width: 100%;
		margin: 0;
		padding: 0;
		font-size: 1.6rem;
		line-height: 19px;
		color: #656565; }

	a:hover {
		color: $light-text; } }

/*- midias ft -*/
.midias-ft {
	font-size: 30px; }


/* -------------------------------------------------------------
 2.0 Container copyright
 ------------------------------------------------------------- */
#sac-wrap {
	.social-wrap {
		margin-top: 7px;
		display: inline-block;
		//a
 }		//loat: left

	.copyright {
		max-width: 90%;
		margin: 33px auto 0 0;
		font-size: 14px;
		font-weight: normal;
		display: block;
		color: #fff;
		@media screen and (max-width: 1199px) {
			font-size: 12px; } }

	.designed {
		float: right;
		padding: 0 20px 0 0;

		span {
			margin: 0 5px 0 0;
			font-size: 1.2rem;
			color: #FFF; }

		a:hover {
			opacity: 0.8; } } }

/* ------------------------------------------------------------
 3.0 Media Queries
 ------------------------------------------------------------ */

/* ----------------------------------------
 Small size
 ---------------------------------------- */
@media screen and (max-width: 767px) {
	.wrap-customer-data {
		.footer-right {
			.fb_iframe_widget {
				margin-left: 4.56%; } } } }

@media screen and (max-width: 600px) {

	.footer-column {
		padding-left: 0;
		padding-right: 0;

		&:first-child {
			padding-right: 15px; }

		&:nth-child(2) {
			padding-left: 8px; }

		.midias-ft {
			h3 {
				margin-top: 15px; } } }

	.container-copyright {

		.copyright {
			padding-left: 0; }

		.designed {
			padding-right: 0; } } }

@media screen and (max-width: 413px) {
	.wrap-customer-data {
		.footer-right {
			.fb_iframe_widget {
				//width: 76.44%
				//margin-left: 10.56%
				display: inline-block;
				overflow: hidden; } } } }

@media screen and (max-width: 374px) {
	.midias-ft {
		.wrap-icon-medias {
			width: 100%;

			li {
				margin: 18px 15px 0 0;
				display: inline-block; } } }

	.wrap-customer-data {
		.footer-right {
			.fb_iframe_widget {
				margin-left: 0px;
				width: 280px; } } } }

#map-footer {
	display: block;
	height: 204px;
	border-bottom: 2px solid #4a96d0;
	border-top: 2px solid #4a96d0;
	position: relative;
	.waze {
		position: absolute;
		right: 0px;
		top: 0px; } }
