/*--------------------------------------------------------------
 *# Normalize
 *-------------------------------------------------------------- */
* {
	box-sizing: border-box; }

html {
	font-size: 62.5%;
	font-family: $secundary-font;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
	max-width: 100%;
	overflow-x: hidden;
	margin-top: 0 !important; }

body {
	margin: 0;
	font-size: 1.6rem;
	font-family: $main-font;
	color: $text-color;
	background: $bg-body;
	overflow-x: hidden; }

article, aside, details, figcaption, figure, footer, header, main, menu, nav, section, summary {
	display: block; }

audio, canvas, progress, video {
	display: inline-block;
	vertical-align: baseline; }

audio:not([controls]) {
	display: none;
	height: 0; }

[hidden], template {
	display: none; }

a {
	background-color: transparent;
	text-decoration: none;
	&:active, &:hover {
		outline: 0!important; } }

abbr[title] {
	border-bottom: 1px dotted; }

b, strong {
	font-weight: bold; }

dfn {
	font-style: italic; }

mark {
	background: #ff0;
	color: #000; }

small {
	font-size: 80%; }

sub {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline; }

sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
	top: -0.5em; }

sub {
	bottom: -0.25em; }

img {
	border: 0; }

svg:not(:root) {
	overflow: hidden; }

figure {
	margin: 1em 40px; }

hr {
	box-sizing: content-box;
	height: 0; }

pre {
	overflow: auto; }

code, kbd, pre, samp {
	font-family: monospace, monospace;
	font-size: 1rem; }

button, input, optgroup, select, textarea {
	color: inherit;
	font: inherit;
	margin: 0; }

button {
	overflow: visible;
	text-transform: none; }

select {
	text-transform: none; }

button, html input[type="button"] {
	-webkit-appearance: button;
	cursor: pointer; }

input {
	&[type="reset"], &[type="submit"] {
		-webkit-appearance: button;
		cursor: pointer; } }

button[disabled], html input[disabled] {
	cursor: default; }

button::-moz-focus-inner {
	border: 0;
	padding: 0; }

input {
	&::-moz-focus-inner {
		border: 0;
		padding: 0; }
	line-height: normal;
	&[type="checkbox"], &[type="radio"] {
		box-sizing: border-box;
		padding: 0; }
	&[type="number"] {
		&::-webkit-inner-spin-button, &::-webkit-outer-spin-button {
			height: auto; } }
	&[type="search"] {
		-webkit-appearance: textfield;
		box-sizing: content-box;
		&::-webkit-search-cancel-button, &::-webkit-search-decoration {
			-webkit-appearance: none; } } }

fieldset {
	border: none;
	margin: 0;
	padding: 0; }

legend {
	border: 0;
	padding: 0; }

textarea {
	overflow: auto; }

optgroup {
	font-weight: bold; }

table {
	border-collapse: collapse;
	border-spacing: 0; }

td, th {
	padding: 0; }

/*--------------------------------------------------------------
 *# Typography
 *-------------------------------------------------------------- */
h1 {
	font-size: 2rem; }

h1, h2, h3, h4, h5, h6 {
	clear: both;
	margin: 0;
	padding: 15px 0 10px 0;
	font-family: $main-font;
	color: $text-color; }

h1 {
	font-size: 3rem; }

h2 {
	font-size: 2.5rem; }

h3 {
	font-size: 2rem; }

h4 {
	font-size: 1.8rem; }

h5 {
	font-size: 1.6rem; }

h6 {
	font-size: 1.5rem; }

button, input, select, textarea {
	color: #404040;
	font-size: 1.6rem;
	line-height: 1.5; }

p {
	margin-bottom: 1.5rem; }

dfn, cite, em, i {
	font-style: italic; }

blockquote {
	margin: 0 1.5em; }

address {
	margin: 0 0 1.5em; }

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 1.5rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em; }

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 1.5rem; }

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help; }

mark, ins {
	background: #fff9c0;
	text-decoration: none; }

big {
	font-size: 125%; }

/*--------------------------------------------------------------
 *# Elements
 *-------------------------------------------------------------- */
blockquote {
	&:before, &:after {
		content: ""; } }

q {
	&:before, &:after {
		content: ""; } }

blockquote, q {
	quotes: "" ""; }

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em; }

ul, ol {
	margin: 0 0 0 0;
	padding: 0 0 0 20px; }

ul {
	list-style: disc; }

ol {
	list-style: decimal; }

li > {
	ul, ol {
		margin-bottom: 0;
		margin-left: 1.5em; } }

dt {
	font-weight: bold; }

dd {
	margin: 0 1.5em 1.5em; }

img {
	height: auto;
	max-width: 100%; }

table {
	margin: 0 0 1.5em;
	width: 100%;

	tr {
		border: 1px solid #e5e5e5;

		td, th {
			padding: 9px 10px;
			text-align: center;
			border: 1px solid #e5e5e5; }

		th {
			color: #666666;
			background-color: #fdfdfd; } } }

/*--------------------------------------------------------------
 *# Forms
 *-------------------------------------------------------------- */

input {
	&[type="button"],
	&[type="reset"],
	&[type="submit"] {
		width: 100%;
		padding: 2px 20px;
		font-size: 1.7rem;
		font-weight: 600;
		color: #fff;
		text-transform: uppercase;
		border: 2px solid #374a87;
		//border-radius: 5px
		background: #374a87;
		box-shadow: none; } }

input {
	&[type="button"]:hover,
	&[type="reset"]:hover,
	&[type="submit"]:hover {
		background: #374a87; } }

input {
	&[type="button"]:focus,
	&[type="reset"]:focus,
	&[type="submit"]:focus {
		background: #374a87; } }

input {
	&[type="button"]:active,
	&[type="reset"]:active,
	&[type="submit"]:active {
		background: #eaeaea; } }

input {
	&[type="text"],
	&[type="email"],
	&[type="url"],
	&[type="password"],
	&[type="search"],
	&[type="tel"],
	&[type="date"], {
		width: 100%;
		height: 40px;
		padding: 0 14px;
		font-size: 1.7rem;
		font-weight: 600;
		color: $dark-text;
		background: $light-text; }

	&[type="file"] {
		width: 100%;
		height: 40px;
		padding: 6px 10px; } }

select {
	width: 100%;
	height: 40px;
	padding: 0 14px;
	font-size: 1.7rem;
	font-weight: 600;
	color: $dark-text;
	background: $light-text; }

textarea {
	width: 100%;
	padding: 0 14px;
	font-size: 1.7rem;
	font-weight: 100;
	color: $dark-text;
	//border: 2px solid #374a87
	//border-radius: 5px
	background: $light-text; }

input {
	&[type="text"]:focus,
	&[type="email"]:focus,
	&[type="url"]:focus,
	&[type="password"]:focus,
	&[type="search"]:focus,
	&[type="tel"]:focus {
		border-color: #737373; } }

textarea:focus {
	border-color: #737373; }


/*--------------------------------------------------------------
 *# Navigation
 *-------------------------------------------------------------- */
/*--------------------------------------------------------------
 *## Links
 *-------------------------------------------------------------- */

a {
	color: $main-color;

	&:hover,
	&:focus,
	&:active {
		color: lighten($main-color, 20%);
		outline: 0!important; } }

/*--------------------------------------------------------------
 *# Accessibility
 *-------------------------------------------------------------- */
/* Text meant only for screen readers. */

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	&:focus {
		background-color: #f1f1f1;
		border-radius: 3px;
		box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
		clip: auto !important;
		color: #21759b;
		display: block;
		font-size: 1.4rem;
		font-weight: bold;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000;
		/* Above WP toolbar. */ } }


/*--------------------------------------------------------------
 *# Clearings
 *-------------------------------------------------------------- */

.cf {
	&:before, &:after {
		content: "";
		display: table; }

	&:after {
		clear: both; } }

/*--------------------------------------------------------------
 *# Widgets
 *-------------------------------------------------------------- */

.widget {
	margin: 0 0 1.5em;
	select {
		max-width: 100%; } }

/* Make sure select elements fit in widgets. */

/* Search widget. */

.widget_search .search-submit {
	display: none; }

/*--------------------------------------------------------------
 *# Content
 *-------------------------------------------------------------- */
/*--------------------------------------------------------------
 *## Posts and pages
 *-------------------------------------------------------------- */

.sticky {
	display: block; }

.hentry {
	margin: 0 0 0 0; }

//.byline, .updated:not(.published)
//isplay: none

.single .byline, .group-blog .byline {
	display: inline; }

.page-content, .entry-content, .entry-summary {
	margin: 1.5em 0 0;
	//padding: 40px 0
	padding: 0;
	//background-color: $light-main
	//background-image: url(../images/bg.jpg)
	//background-position: bottom center
	//background-repeat: no-repeat
	//background-attachment: fixed
	color: $main-color; }

.archive,
.single {
	.entry-content {
		padding-bottom: 0px; } }

.entry-content {
	margin: 0;
	font-family: $secundary-font; }

.entry-footer {
	background: #fff;
	text-align: left;
	margin-top: 25px !important;
	text-transform: capitalize;
	.posted-on {
		a {
			color: #959595; } } }

.single-equipe,
.single-tratamento {
	.entry-footer {
		display: none; } }

.page-links {
	clear: both;
	margin: 0 0 1.5em; }

/*--------------------------------------------------------------
 *## Asides
 *-------------------------------------------------------------- */

.blog .format-aside .entry-title, .archive .format-aside .entry-title {
	display: none; }

/*--------------------------------------------------------------
 *## Comments
 *-------------------------------------------------------------- */

.comment-content a {
	word-wrap: break-word; }

.bypostauthor {
	display: block; }

/*--------------------------------------------------------------
 *# Infinite scroll
 *-------------------------------------------------------------- */
/* Globally hidden elements when Infinite Scroll is supported and in use. */

.infinite-scroll {
	.posts-navigation, &.neverending .site-footer {
		/* Theme Footer (when set to scrolling) */
		display: none; } }

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */

.infinity-end.neverending .site-footer {
	display: block; }

/*--------------------------------------------------------------
 *# Media
 *-------------------------------------------------------------- */

.page-content .wp-smiley, .entry-content .wp-smiley, .comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0; }

/* Make sure embeds and iframes fit their containers. */

embed, iframe, object {
	max-width: 100%; }

/*--------------------------------------------------------------
 *## Captions
 *-------------------------------------------------------------- */

.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
	img[class*="wp-image-"] {
		display: block;
		margin: 0 auto; } }

.wp-caption-text {
	text-align: center; }

.wp-caption .wp-caption-text {
	margin: 0.8075em 0; }

/*--------------------------------------------------------------
 *## Galleries
 *-------------------------------------------------------------- */

.gallery {
	margin-bottom: 1.5em; }

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%; }

.gallery-columns-2 .gallery-item {
	max-width: 50%; }

.gallery-columns-3 .gallery-item {
	max-width: 33.33%; }

.gallery-columns-4 .gallery-item {
	max-width: 25%; }

.gallery-columns-5 .gallery-item {
	max-width: 20%; }

.gallery-columns-6 .gallery-item {
	max-width: 16.66%; }

.gallery-columns-7 .gallery-item {
	max-width: 14.28%; }

.gallery-columns-8 .gallery-item {
	max-width: 12.5%; }

.gallery-columns-9 .gallery-item {
	max-width: 11.11%; }

.gallery-caption {
	display: block; }


/*--------------------------------------------------------------
 *## Woo Commerce
 *-------------------------------------------------------------- */

.woocommerce {
	span.onsale,
	.add_to_cart_button {
		display: none !important; }

	ul.products {
		li.product {
			.price,
			.button.product_type_grouped {
				display: none; } } }

	div.product {
		p.price, span.price, .cart {
			display: none; } }

	.related.products {
		h2,
		.products {
			display: none; } } }

.dropdown-content {
	display: none;
	&.-is-active {
		display: inline-block; } }
